// Import Styled
import styled from 'styled-components';

export const BlockSlack = styled.div`
	border-radius: 10px;
	background: linear-gradient(201.71deg, #8F9DF8 0%, #92A0FF 100%);
	box-shadow: 0 26px 51px 0 rgba(0,0,0,0.08);
	padding: 24px 16px;
	color: #fff;
	text-align: center;
	h5{
		font-weight: 700;
		font-size: 18px;
		line-height: 25px;
		margin-left: 16px;
	}
	p{
		opacity: 0.75;
		font-size: 13px;
		line-height: 20px;
		margin-left: 24px;
	}
`;